<script setup lang="ts">
import {AppGrid} from '@/components/grid';
import {TransactionService} from '@/services/transaction.service';
import {pickGridColumns} from '@/logic/common';
import {transactionConfig} from '@/logic/transaction-config';

const config = pickGridColumns(
  transactionConfig,
  [
    'id',
    'orderId',
    'amount',
    'currency',
    'oldBalance',
    'newBalance',
    'comment',
    'createdAt',
  ],
  {
    getData: () => useService(TransactionService).getMyTransactions(),
    title: 'Мои транзакции',
  },
);
</script>

<template>
  <AppGrid :config="config" />
</template>
